
.personality-setting {
  width: 100%;
}
.personality-setting .group-select-wrapper {
  position: relative;
  width: 100%;
  height: 50px;
}
.personality-setting .group-select-wrapper .select {
  position: absolute;
  top: 0;
  right: 100px;
  width: 100px;
}
.personality-setting .m-setting-wrapper {
  padding-left: 24px;
}
.personality-setting .m-setting-wrapper .title {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 24px;
}
.personality-setting .m-setting-wrapper .img-wrapper {
  margin-left: 20px;
  margin-bottom: 80px;
  cursor: pointer;
}
.personality-setting .m-setting-wrapper .home-img-wrapper {
  width: 300px;
  min-height: 300px;
  border: 1px solid #ccc;
}
.personality-setting .m-setting-wrapper .home-img-wrapper img {
  width: 100%;
}
.personality-setting .m-setting-wrapper .blog-img-wrapper {
  width: 500px;
  min-height: 200px;
  border: 1px solid #ccc;
}
.personality-setting .m-setting-wrapper img {
  width: 100%;
}


