
.messageBoard {
  width: 100%;
  min-height: 100%;
  padding: 40px;
  overflow: scroll;
}
.messageBoard .messageItem {
  position: relative;
  margin: 20px;
}
.messageBoard .messageItem .btn-close {
  position: absolute;
  padding: 10px;
  z-index: 5;
  top: 0px;
  right: 0px;
  cursor: pointer;
  color: #000;
}
.messageBoard .messageItem .btn-close:hover {
  color: #afafaf;
}
.messageBoard .messageItem .text {
  padding-left: 8px;
}


