
.blogShow {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 50;
  background-color: #fff;
}
.blogShow.fade-enter-active,
.blogShow.fade-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.blogShow.fade-enter,
.blogShow.fade-leave-to {
  opacity: 0;
}
.blogShow .close_wrapper {
  position: absolute;
  z-index: 50;
  right: 100px;
  top: 50px;
  color: #000;
  cursor: pointer;
}
.blogShow .close_wrapper .close_btn {
  width: 80px;
  height: 80px;
}
.blogShow .content-wrapper {
  width: 100%;
  height: 100%;
  padding: 20px 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.blogShow .content-wrapper .content-left-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 400px;
          flex: 0 0 400px;
  padding-top: 50px;
  padding-left: 30px;
  height: 100%;
}
.blogShow .content-wrapper .content-left-wrapper .imgLoader {
  width: 200px;
  height: 200px;
}
.blogShow .content-wrapper .content-left-wrapper .imgLoader img {
  width: 100%;
  height: 100%;
}
.blogShow .content-wrapper .content-right-wrapper {
  padding-left: 40px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
}


