
.newBlog {
  width: 100%;
  overflow: scroll;
}
.newBlog .input-wrapper {
  width: 100%;
  height: 100%;
  padding: 20px 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.newBlog .input-wrapper .image-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 200px;
          flex: 0 0 200px;
  width: 200px;
  height: 300px;
}
.newBlog .input-wrapper .image-wrapper .imgLoader {
  width: 200px;
  height: 200px;
}
.newBlog .input-wrapper .image-wrapper .imgLoader img {
  width: 100%;
  height: 100%;
}
.newBlog .input-wrapper .desc-wrapper {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 0 50px;
}
.newBlog .input-wrapper .desc-wrapper .send-btn {
  position: absolute;
  width: 150px;
  height: 50px;
  right: 5%;
  bottom: 20%;
}


