
.imgTailor {
  position: fixed;
  z-index: 999;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: rgba(52,80,103,0.5);
}
.imgTailor .img-wrapper {
  position: absolute;
  left: 30%;
  top: 20px;
}
.imgTailor .img-wrapper img {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.imgTailor .img-wrapper .bottom-img {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}
.imgTailor .img-wrapper .top-img-wrapper {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.imgTailor .img-wrapper .top-img-wrapper .top-img {
  position: absolute;
  left: 0px;
  top: 0px;
  clip: rect(36px 370px 286px 101px);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.imgTailor .img-wrapper .filter {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.imgTailor .img-wrapper .filter .tailor_rec {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 200px;
  height: 200px;
  border: 1px solid #000;
  cursor: move;
}
.imgTailor .img-wrapper .filter .tailor_rec .topLeft {
  position: absolute;
  top: -6px;
  left: -6px;
  border: 1px solid #fff;
  width: 6px;
  height: 6px;
  cursor: nw-resize;
}
.imgTailor .img-wrapper .filter .tailor_rec .topRight {
  position: absolute;
  top: -6px;
  right: -6px;
  border: 1px solid #fff;
  width: 6px;
  height: 6px;
  cursor: sw-resize;
}
.imgTailor .img-wrapper .filter .tailor_rec .bottomLeft {
  position: absolute;
  bottom: -6px;
  left: -6px;
  border: 1px solid #fff;
  width: 6px;
  height: 6px;
  cursor: ne-resize;
}
.imgTailor .img-wrapper .filter .tailor_rec .bottomRight {
  position: absolute;
  bottom: -6px;
  right: -6px;
  border: 1px solid #fff;
  width: 6px;
  height: 6px;
  cursor: se-resize;
}
.imgTailor .img-wrapper .filter .tailor_rec .rec_width {
  position: absolute;
  bottom: 30px;
  right: -110px;
  width: 100px;
  height: 24px;
  color: #07f589;
  cursor: pointer;
}
.imgTailor .img-wrapper .filter .tailor_rec .rec_height {
  position: absolute;
  bottom: 0px;
  right: -110px;
  width: 100px;
  height: 24px;
  color: #07f589;
  cursor: pointer;
}
.imgTailor .ok_btn {
  position: absolute;
  right: 10%;
  top: 10%;
}


