
.baseInfo {
  width: 100%;
  min-height: 100%;
}
.baseInfo .avatar-wrapper {
  position: relative;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  margin: 0 auto;
}
.baseInfo .avatar-wrapper .avatar {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.baseInfo .avatar-wrapper .img-file {
  position: absolute;
  border-radius: 50%;
  left: 0px;
  top: 0px;
  opacity: 0;
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.baseInfo .content-wrapper {
  padding: 40px 20% 0 20%;
}
.baseInfo .content-wrapper .name-wrapper,
.baseInfo .content-wrapper .signature-wrapper,
.baseInfo .content-wrapper .address-wrapper {
  padding: 0 20%;
}
.baseInfo .content-wrapper .address-wrapper {
  position: relative;
}
.baseInfo .content-wrapper .address-wrapper .baseInfo_btn {
  position: absolute;
  bottom: 0px;
  right: 0px;
}
.baseInfo .password-wrapper {
  padding: 40px 20% 0 20%;
}
.baseInfo .password-wrapper .old-password-1,
.baseInfo .password-wrapper .old-password-2,
.baseInfo .password-wrapper .new-password {
  padding: 0 20%;
}
.baseInfo .password-wrapper .new-password {
  position: relative;
}
.baseInfo .password-wrapper .new-password .password_btn {
  position: absolute;
  right: 0px;
  bottom: 0;
}


