
.editor {
  padding: 40px;
}
.editor .quill-editor-wrapper {
  width: 100%;
  height: 100%;
}
.editor .ql-toolbar .ql-font span[data-value="Sans Serif"]::before {
  content: 'Sans Serif';
  font-family: "Sans Serif";
}
.editor .ql-toolbar .ql-font span[data-value="Inconsolata"]::before {
  content: 'Inconsolata';
  font-family: "Inconsolata";
}
.editor .ql-toolbar .ql-font span[data-value="Roboto"]::before {
  content: 'Roboto';
  font-family: "Roboto";
}
.editor .ql-toolbar .ql-font span[data-value="Mirza"]::before {
  content: 'Mirza';
  font-family: "Mirza";
}
.editor .ql-toolbar .ql-font span[data-value="Arial"]::before {
  content: 'Arial';
  font-family: "Arial";
}
.editor .ql-toolbar .ql-font span[data-value="Helvetica"]::before {
  content: 'Helvetica';
  font-family: "Helvetica";
}


